body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.banner-primary{font-family:'Josefin Sans', sans-serif;text-align:center;height:530px}.banner-primary-text{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100%;color:white}.banner-primary-title{font-weight:600;font-size:3.125rem;line-height:3.125rem;letter-spacing:1.6rem;margin:1%}.banner-primary-subtitle{font-weight:400;font-size:1.25rem;line-height:1.25rem;letter-spacing:0.6rem;margin:1%}.banner-primary-image{position:relative;width:100%;height:inherit;background-size:cover;background-position:center;z-index:-99}@media screen and (max-width: 991px) and (min-width: 768px){.banner-primary-title{font-size:2rem;line-height:2rem;letter-spacing:1rem}.banner-primary-subtitle{font-size:1rem;line-height:1.2rem;letter-spacing:0.7rem}}@media screen and (max-width: 767px) and (min-width: 576px){.banner-primary-title{font-size:2rem;line-height:1.5rem;letter-spacing:0.75rem}.banner-primary-subtitle{font-size:0.8rem;line-height:1.2rem;letter-spacing:0.5rem}}@media screen and (max-width: 575px){.banner-primary-title{font-size:1.5rem;line-height:1.5rem;letter-spacing:0.4rem}.banner-primary-subtitle{font-size:0.75rem;line-height:1.2rem;letter-spacing:0.4rem}}

.App{text-align:center;position:relative;padding:0}.norm-container{position:relative;margin:0 14%;overflow:hidden}.body-container{padding-top:2.5rem;overflow:hidden}.end-container{padding-top:2rem;padding-bottom:1rem}.sub-container{position:relative;margin:0 4.5%}.translucent-overlay{position:absolute;top:0;bottom:0;left:0;right:0;overflow:hidden;z-index:-98;background-color:rgba(132,132,132,0.5);box-sizing:content-box}.primary-text{font-family:Montserrat, sans-serif;font-size:1rem;line-height:200%;letter-spacing:0.02em;padding:0 6.5%;text-align:justify}@media screen and (max-width: 575px){.primary-text{font-size:0.95rem;line-height:200%;letter-spacing:0.02rem}}

.banner-standard{font-family:'Josefin Sans', sans-serif;text-align:center;font-weight:700;font-size:1.875rem;line-height:200%;letter-spacing:0.5em;height:433px;background-color:rgba(0,0,0,0);transition:background-color 0.7s}.banner-standard:hover{cursor:pointer;background-color:rgba(0,0,0,0.1)}.banner-standard-white{color:white}.banner-standard-black{color:black}.banner-image{position:relative;width:100%;height:inherit;background-size:cover;background-position:center;z-index:-99}.banner-text{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100%}.banner-text-black{position:absolute;margin:20% 0 20% 11rem}@media screen and (max-width: 991px) and (min-width: 768px){.banner-text{font-size:2rem;line-height:2rem;letter-spacing:1rem}}@media screen and (max-width: 767px) and (min-width: 576px){.banner-text{font-size:2rem;line-height:2rem;letter-spacing:1rem}}@media screen and (max-width: 575px){.banner-text{font-size:2rem;line-height:2rem;letter-spacing:0.5rem}}@media screen and (max-width: 991px){.banner-text-black{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100%;position:relative;margin:0}.banner-text-black h2{color:white;text-shadow:-0.9px 0 gray, 0 0.9px gray, 0.9px 0 gray, 0 -0.9px gray}}

.footer-text{font-family:'Josefin Sans', sans-serif;font-weight:400;font-size:14px;line-height:200%}

.navigation-norm{display:flex;flex-direction:row;justify-content:flex-start;padding:0}.navigation-tab{font-family:'Josefin Sans', sans-serif;padding-right:4%;padding-top:2rem;padding-bottom:calc(1.4rem - 0.1em)}.navigation-tab a{color:black;text-decoration:none}.navigation-tab h4{font-weight:300;font-size:1rem}.current-tab h4{border-bottom-width:1px;border-bottom-style:solid;padding-bottom:0.1em}

.card{display:flex;flex-direction:column;flex-wrap:wrap;margin:0.4rem}

.portfolio-title{font-weight:600;font-size:1.5625rem;line-height:200%;letter-spacing:0.02em;margin-bottom:1rem}.portfolio-name{display:flex;justify-content:center;align-items:center;flex-direction:column;font-weight:700;font-size:1.875rem;line-height:1.875rem;letter-spacing:0.405em;color:white;height:100%;z-index:1}.divider{width:5%;height:4rem;position:relative}.portfolio-name h3{cursor:default}.portfolio-master{font-family:'Josefin Sans', sans-serif;text-align:center;overflow:hidden}.portfolio-preview-row{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;padding:0 1rem}.portfolio-preview-image{position:relative;width:100%;height:100%;background-size:cover;background-position:center;z-index:-99}.portfolio-previews{background-color:rgba(0,0,0,0);transition:background-color 0.7s;width:47.5%;min-width:448px;margin-bottom:2rem;padding:0;box-sizing:border-box;position:relative}.portfolio-previews:first-child{margin-right:1rem}.portfolio-previews:last-child{margin-left:1rem}.portfolio-previews:hover{cursor:pointer;background-color:rgba(0,0,0,0.1)}@media screen and (max-width: 992px){.portfolio-previews:first-child{margin-right:auto;margin-left:auto}.portfolio-previews:last-child{margin-left:auto;margin-right:auto;margin-bottom:0}}@media screen and (max-width: 576pc){.portfolio-previews{width:100%}}@media screen and (max-width: 991px) and (min-width: 768px){.portfolio-name{font-size:2rem;line-height:2rem}}@media screen and (max-width: 767px) and (min-width: 576px){.portfolio-name{font-size:2rem;line-height:2rem}}@media screen and (max-width: 575px){.portfolio-name{font-size:1rem;line-height:2rem}}

.resume{width:100%;overflow:hidden;display:flex;flex-direction:row;justify-content:center}.resume-download{text-decoration:none;color:black;font-family:Montserrat, sans-serif}.resume-help{display:flex;flex-direction:row;justify-content:center;color:white;background-color:black;width:10rem;padding:0.5rem;border-radius:5px}.resume-help a{text-decoration:none;color:white}

